<template>
    <router-link to="/"><div id="zw"></div></router-link>
    <div id="nav">
        <div id="navitems">
        <router-link to="/">PROJECTS</router-link>
        <router-link to="/art">ART</router-link>
        <router-link to="/about">ABOUT</router-link>
        <router-link to="/contact">CONTACT</router-link>
        </div>
    </div>
</template>
<style scoped>
#nav {
    display: flex;
    justify-content: flex-end;
    width: 50%;
    float:right;
    margin:50px;
    margin-right:100px;
}
#navitems {
    display: flex;
    justify-content: space-around;
}
#navitems > a {
    text-decoration: none;
    color: #FFFFFF;
    padding-left: 10px;
    padding-right: 10px;
    font-family: 'Michroma', sans-serif;
    position: relative;
}
#navitems > a::after {
    border-radius: 1em;
    border-top: .1em solid #FFFFFF;
    content: "";
    position: absolute;
    right: 100%;
    bottom: -.2em;
    left: .6em;
    transition: right .4s cubic-bezier(0,.5,0,1);
}
#navitems > a:hover::after {
    right: .6em;
}
#zw {
    width:200px;
    margin:50px;
    margin-left:100px;
    background-image: url("../assets/zw.png");
    background-repeat: no-repeat;
    background-size: 200px 74px;
    height:74px;
    display:inline-block;
}
#zw:hover {
    background-image: url("../assets/zw2.png");
    width: 218px;
    background-size: 218px 74px;
}
</style>
<script>
    export default {
        name: 'Nav',
            mounted() {
            const script = function (p5) {
            let mouseRadius = 50;
            let yoff = 0;

            p5.setup = () => {
                let canvas = p5.createCanvas(document.body.clientWidth,1000);
                canvas.position(0,0);
                canvas.style('z-index', '-1');
                p5.frameRate(24);
            }

            p5.windowResized = () => {
                p5.resizeCanvas(document.body.clientWidth,1000);
            }

            p5.draw = () => {
                p5.clear();
                p5.background(0);
                for (let y = p5.height/(p5.height/25)*-1; y < p5.height; y+=p5.height/(p5.height/25)) {
                    p5.noFill();
                    p5.stroke(90);
                    p5.strokeWeight(1);
                    for (let x = 0; x <= p5.width+p5.width/(p5.width/25); x+=p5.width/(p5.width/25)) {
                        if (p5.dist(x,y,p5.mouseX,p5.mouseY) < mouseRadius) {
                            p5.ellipse(x,y + 25*p5.sin(x + yoff + p5.dist(x,y,p5.mouseX,p5.mouseY)),3);
                        } else {
                            p5.ellipse(x,y + 25*p5.sin(x + yoff),1);
                        }
                        
                    }
                }
                yoff += .01;
            }
            }
            const P5 = require('p5');
            new P5(script);
        }
    }
</script>